<template>
  <div
    class="register-input-wrapper"
    :class="{ focus: isFocus, 'error-input': isError }"
  >
    <span class="left-content">
      <slot name="left"></slot>
    </span>
    <input
      ref="input"
      v-model.trim="text"
      class="register-input"
      :type="type"
      :name="name"
      :maxlength="maxLength"
      :placeholder="placeholder"
      :pattern="pattern"
      v-bind="$attrs"
      @focus="onFocus"
      v-on="listeners"
      @keyup.enter="$emit('submit')"
      @blur="onBlur"
      @input="onInput"
    />

    <span class="right-content">
      <!-- Android会触发touchstart事件，ios会触发click事件，保证清除文本并执行聚焦的操作 -->
      <i
        v-show="text.length && isFocus"
        class="super-app-iconfont super-app-icon-clear"
        @touchstart="clear"
        @click="clear"
      ></i>
      <slot name="right"></slot>
    </span>
  </div>
</template>

<script>
let lastText = '';

export default {
  name: 'LoginInput',
  model: { prop: 'value', event: 'change' },
  props: {
    type: { type: String, default: 'text' },
    pattern: { type: String, default: '' },
    value: { type: String, default: '' },
    placeholder: { type: String, default: '' },
    name: { type: String, default: '' },
    maxLength: { type: [String, Number], default: '' },
    showClear: { type: Boolean, default: true },
    isError: { type: Boolean, default: false },
  },
  data() {
    return {
      isFocus: false,
    };
  },
  computed: {
    text: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('change', value);
      },
    },
    listeners() {
      return {
        ...this.$listeners,
        change: () => {},
      };
    },
  },
  watch: {
    text(val) {
      if (
        String(this.name)
          .toLowerCase()
          .indexOf('pwd') < 0
      ) {
        this.text = val.slice(0, 20);
      }
    },
  },

  methods: {
    clear() {
      this.text = '';
      // setTimeout(() => {
      this.$refs.input.focus();
      // }, 100);
    },

    onBlur() {
      this.isFocus = false;
      /** for ios 键盘收回界面异常 */
      window.scroll(0, 0);
    },

    onFocus() {
      this.isFocus = true;
    },
    onInput($event) {
      if (this.type.toLowerCase() === 'number') {
        $event.target.value = $event.target.value.replace(/[^0-9]/gi, '');
        if ($event.target.value.length > this.maxLength) {
          this.text = lastText;
        } else {
          lastText = $event.target.value;
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
@import '~@styles/variable.scss';

.error-input {
  &::after {
    border-bottom: 2px solid $color-error !important;
  }
}

.register-input-wrapper {
  display: flex;
  position: relative;
  padding: 6px 0;
  overflow: hidden;
  text-align: center;
  width: 100%;

  &::after,
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid #efeff4;
  }

  &::after {
    transform: translate3d(-100%, 0, 0);
    transition: transform 0.3s;
    border-bottom: 2px solid $color-theme;
  }

  .register-input {
    display: block;
    border: 0;
    border-radius: 0;
    outline: 0;
    font-size: 16px;
    background: transparent;
    flex: 1;
    color: #000000;
  }

  .right-content {
    position: absolute;
    top: 55%;
    right: 0px;
    transform: translateY(-50%);
    margin-top: 2px;

    .super-app-iconfont {
      font-size: 18px;
      color: #c8c8c8;
    }
  }
}

.focus {
  &::after {
    transform: translate3d(0, 0, 0);
  }
}
</style>
